import React from "react";
import { Row, Col, Form, Button } from "react-bootstrap";

const Todos = ({Todo, handleTodoChange, handleTodoSubmit, id}) => {
    return(
        <div className="mt-5">
            <Row>
                <Col>
                    <h4>{id ? "Edit Data" : "Tambah Data"}</h4>
                    <hr />
                </Col>
            </Row>
            <Row>
                <Col>
                    <Form onSubmit={handleTodoSubmit}>
                        <Form.Group controlId="Todo">
                            <Form.Label>ToDo List</Form.Label>
                            <Form.Control 
                                type="text"
                                name="Todo"
                                value={Todo}
                                onChange={(event) => handleTodoChange(event)}
                            />
                        </Form.Group><br></br>
                        <Button variant="primary" type="submit" onClick={() => {this.handleTodoSubmit() }}>Submit</Button>
                    </Form>
                </Col>
            </Row>
        </div>
    );
};

export default Todos;