import React, { Component } from 'react';
import NavbarComponent from './NavbarComponent';
import Tabel from './Tabel';
import Todos from './Todos';

export default class Crud extends Component {
    constructor(props){
      super(props);
  
      this.state = {
        Todos: [],
        todo: '',
        id: '',
      };
     
      this.completeData = this.completeData.bind(this);
      this.handleTodoSubmit = this.handleTodoSubmit.bind(this);
    }
  
    handleTodoChange = (event) => {
      this.setState({
        todo: event.target.value,
      });
    };
  
    handleTodoSubmit = (event) => {
      event.preventDefault();
  
      if (this.state.id === ""){
        this.setState({
          Todos: [
            ...this.state.Todos, {
              id: this.state.Todos.length + 1,
              todo: this.state.todo,
            },
          ],
        });
      } else {
        const todoTidakDipilih = this.state.Todos.filter((todo) => todo.id !== this.state.id)
        .map((filtertodo) => {
          return filtertodo;
        });
  
        this.setState({
          Todos: [
            ...todoTidakDipilih, {
              id: this.state.Todos.length + 1,
              todo: this.state.todo,
            },
          ],
        });
      }
      
      this.setState({
        todo: "",
        id: "",
      });
    };
  
    editData = (id) => {
      const todoDipilih = this.state.Todos.filter((todo) => todo.id === id)
      .map((filtertodo) => {
        return filtertodo;
      });
  
      this.setState({
        todo: todoDipilih[0].todo,
        id: todoDipilih[0].id,
      });
    };
  
    deleteData = (id) => {
      const NewTodo = this.state.Todos.filter((todo) => todo.id !== id)
      .map((filtertodo) => {
        return filtertodo;
      });
  
      this.setState({
        Todos: NewTodo
      });
    };

    completeData = (id) => {
      const updatedTodo = Todos.map(todo => {
        if (todo.id === id) {
          todo.isComplete = !todo.isComplete;
        }
        return todo;
      });
      this.setState(updatedTodo);
    };
  
    render(){
      return (
        <div>
          <NavbarComponent />
          <div className="container mt-4">
            <Tabel Todo={this.state.Todos} editData={this.editData} deleteData={this.deleteData} completeData={this.completeData} />
            <Todos 
              {...this.state}
              handleTodoChange={this.handleTodoChange}
              handleTodoSubmit={this.handleTodoSubmit}
            />
          </div>
        </div>
      );
    }
  }