import React from "react";
import { Table } from "react-bootstrap";

const Tabel = ({ Todo, editData, deleteData, completeData }) => {
    return (
        <Table striped bordered hover>
            <thead>
                <tr>
                    <th>No</th>
                    <th>Todo List</th>
                </tr>
            </thead>
            <tbody>
                {Todo.map((todo, index) => {
                    return (
                        <tr key={index}>
                            <td>{index + 1}</td>
                            <td>{todo.todo}</td>
                            <td>
                                <button className="btn btn-warning ms-2" onClick={() => editData(todo.id) }>Edit</button>
                                <button className="btn btn-danger ms-2" onClick={() => deleteData(todo.id) }>Delete</button>
                                <button className="btn btn-success ms-2" onClick={() => completeData(todo.id)}>Complete</button>
                            </td>
                        </tr>
                    );
                })}
            </tbody>
        </Table>
    );
};

export default Tabel;